import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'

import thumb01 from '../../assets/images/Instagram_icon.jpg'
import thumb02 from '../../assets/images/social_facebook_box_white.png'

const Header = () => (
  <div
    style={{
      background: '#00778b',
      marginBottom: '1.45rem',
    }}
  >
    <div
      style={{
        margin: '0 auto',
        maxWidth: 960,
        padding: '1.45rem 1.0875rem',
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: '#000',
            textDecoration: 'none',
          }}
        >
          <a href="https://www.instagram.com/roxieta_ig/"><img src={thumb01} /></a> &nbsp;
          <a href="https://www.facebook.com/roxieta.catalogo/"><img src={thumb02} /></a> 
        </Link>
      </h1>
    </div>
  </div>
)

export default Header



